
"use client";

import {
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarFooter,
} from "@/components/ui/sidebar";
import { usePathname } from "next/navigation";
import Link from "next/link";
import { Separator } from "@/components/ui/separator";
import { Button } from "../ui/button";
import { LogOut } from "lucide-react";
import { navLinks, type Role } from "@/lib/constants";
import { Badge } from "../ui/badge";

interface AppSidebarProps {
  role: Role;
}

export function AppSidebar({ role }: AppSidebarProps) {
  const pathname = usePathname();
  const links = navLinks[role];

  return (
    <Sidebar>
      <SidebarHeader className="p-4">
        <div className="flex items-center gap-2">
            <div className="w-10 h-10 bg-primary/90 rounded-full flex items-center justify-center font-bold text-primary-foreground text-lg font-headline">
                M
            </div>
          <div className="flex flex-col">
            <h2 className="text-lg font-semibold font-headline">MediTrack Pro</h2>
            <Badge variant="secondary" className="w-fit">{role.charAt(0).toUpperCase() + role.slice(1)}</Badge>
          </div>
        </div>
      </SidebarHeader>
      <Separator />
      <SidebarContent className="p-4">
        <SidebarMenu>
          {links.map((link) => (
            <SidebarMenuItem key={link.href}>
              <SidebarMenuButton
                asChild
                isActive={pathname === link.href || (link.href !== `/${role}` && pathname.startsWith(link.href))}
                tooltip={link.label}
              >
                <Link href={link.href}>
                  <link.icon className="w-5 h-5" />
                  <span>{link.label}</span>
                </Link>
              </SidebarMenuButton>
            </SidebarMenuItem>
          ))}
        </SidebarMenu>
      </SidebarContent>
      <Separator />
      <SidebarFooter className="p-4">
        <Button variant="ghost" className="w-full justify-start gap-2" asChild>
          <Link href="/">
            <LogOut className="w-5 h-5" />
            <span>Logout</span>
          </Link>
        </Button>
      </SidebarFooter>
    </Sidebar>
  );
}

