class PatientManager {
    private patients: Patient[];
    private subscribers: Function[] = [];

    constructor(initialPatients: Patient[]) {
        this.patients = initialPatients;
    }

    getPatients() {
        return this.patients;
    }

    updatePatientCondition(patientId: string, condition: string) {
        const index = this.patients.findIndex(p => p.id === patientId);
        if (index !== -1) {
            this.patients[index].condition = condition;
            this.notify();
        }
    }

    subscribe(callback: Function) {
        this.subscribers.push(callback);
        return () => {
            this.subscribers = this.subscribers.filter(sub => sub !== callback);
        };
    }

    private notify() {
        this.subscribers.forEach(callback => callback(this.patients));
    }
}

