class LabTestManager {
    private labTests: LabTest[];
    private subscribers: Function[] = [];

    constructor(initialTests: LabTest[]) {
        this.labTests = initialTests;
    }

    getLabTests() {
        return this.labTests;
    }

    addLabTest(testRequest: Omit<LabTest, 'id' | 'price' | 'invoiced' | 'collected' | 'status'> & { doctor: string }) {
        const newTest: LabTest = {
            ...testRequest,
            id: `lab-${Date.now()}`,
            collected: new Date().toISOString().split('T')[0],
            status: 'Pending',
            price: Math.floor(Math.random() * 100) + 50, // Mock price
            invoiced: false,
        };
        this.labTests.unshift(newTest);
        
        const patient = detailedPatients.find(p => p.name === newTest.patient);
        patient?.labTests.unshift(newTest);
        
        // Also add to patient's medical history
        patient?.medicalHistory.unshift({
            date: newTest.collected,
            event: `Lab Test Ordered: ${newTest.test}`,
            details: `Instructions: ${newTest.instructions}`,
            doctor: testRequest.doctor
        });

        this.notify();
    }

    updateLabTest(testId: string, status: LabTest['status'], results?: string) {
        const testIndex = this.labTests.findIndex(t => t.id === testId);
        if (testIndex !== -1) {
            this.labTests[testIndex].status = status;
            if (results) {
                this.labTests[testIndex].results = results;
            }

            // If completed, add results to medical history
            if (status === 'Completed') {
                const test = this.labTests[testIndex];
                const patient = detailedPatients.find(p => p.name === test.patient);
                patient?.medicalHistory.unshift({
                    date: new Date().toISOString().split('T')[0],
                    event: `Lab Test Results: ${test.test}`,
                    details: `Results: ${results}`,
                    doctor: 'Lab'
                });
            }
            this.notify();
        }
    }

    subscribe(callback: (tests: LabTest[]) => void) {
        this.subscribers.push(callback);
        return () => {
            this.subscribers = this.subscribers.filter(sub => sub !== callback);
        };
    }

    private notify() {
        this.subscribers.forEach(callback => callback(this.labTests));
    }
}

export const labTestManager = new LabTestManager(initialLabTests);


